<template>
  <div class="ExperimentPage py-3">
    <div class="container-fluid">
      <div class="row">
        <div class="spectrometer">
          <v-stage ref="stage" id="stage" :config="configKonva">
            <v-layer ref="layer" id="layer">
              <v-rect
                :config="configBackground"
              ></v-rect>
              <v-image
                :config="configSlit"
              ></v-image>
              <v-image
                 v-if="toggleLight == true && slit <=5 && slit>=2 && telescopeFocus == 0 && Grating == false && Mirror == false"
                :config="configSlitLight"
              ></v-image>
              <v-image
                 v-if="toggleLight == true && slit <=5 && slit>=2 && telescopeFocus == 0 && Grating == false && Mirror == true"
                :config="configSlitLightMirror"
              ></v-image>
              <v-image
                v-if="toggleLight == true && slit <=5 && slit>=2 && telescopeFocus == 0 && Grating == true"
                :config="configViolet"
              ></v-image>
              <v-image
                v-if="toggleLight == true && slit <=5 && slit>=2 && telescopeFocus == 0 && Grating == true"
                :config="configBlue"
              ></v-image>
              <v-image
                v-if="toggleLight == true && slit <=5 && slit>=2 && telescopeFocus == 0 && Grating == true"
                :config="configGreen"
              ></v-image>
              <!-- <v-image
                v-if="toggleLight == true && slit <=5 && slit>=2 && telescopeFocus == 0 && Grating == true"
                :config="configYellow"
              ></v-image> -->
              <v-image
                v-if="toggleLight == true && slit <=5 && slit>=2 && telescopeFocus == 0 && Grating == true"
                :config="configViolet2"
              ></v-image>
              <v-image
                v-if="toggleLight == true && slit <=5 && slit>=2 && telescopeFocus == 0 && Grating == true"
                :config="configBlue2"
              ></v-image>
              <v-image
                v-if="toggleLight == true && slit <=5 && slit>=2 && telescopeFocus == 0 && Grating == true"
                :config="configGreen2"
              ></v-image>
              <!-- <v-image
                v-if="toggleLight == true && slit <=5 && slit>=2 && telescopeFocus == 0 && Grating == true"
                :config="configYellow2"
              ></v-image> -->
              <v-image
                :config="configSlitBackground"
              ></v-image>
              <v-image :config="configCollimator"></v-image>
              <v-image :config="configTelescope"></v-image>
              <v-image
                :config="configChavronClockwise"
                @click="telescopeClockwise()"
              ></v-image>
              <v-image
                :config="configChavronAntiClockwise"
                @click="telescopeAntiClockwise()"
              ></v-image>
               <v-image
                :config="configFocusRight"
                @click="focusRight()"
              ></v-image>
              <v-image
                :config="configFocusLeft"
                @click="focusLeft()"
              ></v-image>
              <v-image
                :config="configSlitOpen"
                @mouseDown="slitOpen()"
              ></v-image>
              <v-image
                :config="configSlitClose"
                @click="slitClose()"
              ></v-image>
              <v-image :config="configCalibrate"></v-image>
              <v-image :config="configVernier"></v-image>
              <v-image :config="configTable" v-if="Grating == false"></v-image>
              <v-image :config="configGratingTable" v-else></v-image>
              <v-image :config="configMirrorTable" v-if="Mirror == true "></v-image>
              <v-image
                :config="configSlitTableLeft"
                @click="tableAntiClockwise()"
              ></v-image>
              <v-image
                :config="configSlitTableRight"
                @click="tableClockwise()"
              ></v-image>
              <v-image
                :config="configCalibrateZoom"
              ></v-image>
              <v-image
                :config="configVernierZoom"
              ></v-image>
              <v-image
                :config="configPowerSource"
              ></v-image>
              <v-image
                v-if="toggleLight == false"
                :config="configLightOff"
              ></v-image>
              <v-image v-else :config="configLightOn"></v-image>
              <v-image
                v-if="toggleLight == false"
                :config="configToggleBtnOff"
                @click="switchLight()"
              ></v-image>
              <v-image
                v-else
                :config="configToggleBtnOn"
                @click="switchLight()"
              ></v-image>
              <v-image
                :config="configSpectrometer"
              ></v-image>
              
              <v-line 
                :config="configLine1"
              ></v-line>
              <v-line 
                :config="configLine2"
              ></v-line>
              <v-line 
                :config="configLineVertical"
              ></v-line>
            </v-layer>
          </v-stage>
        </div>
        <div class="setup">
          <div class="card">
            <div class="card-header">
              Settings
            </div>
            <div class="card-body">
                <ul class="list-group list-group-flush">
                    <li class="list-group-item">
                        <div class="row">
                            <div class="col-lg-4 col-md-12 d-flex align-items-center">
                                <h5 class="card-title pr-3">Telescope Focus: </h5>
                            </div>
                            <div class="col-lg-8 col-md-12 d-flex align-items-center bluredImage"  :style="[this.telescopeFocus >= 0 ? { filter: 'blur('+ telescopeFocus +'px)' } :  {filter: 'blur('+ telescopeFocus*-1 +'px)'}]">
                            </div>
                        </div>
                    </li>
                    <li class="list-group-item">
                        <div class="row">
                            <div class="col-lg-6 col-md-12 d-flex align-items-center">
                                <h5 class="card-title pr-3">Slit : </h5>
                            </div>
                            <div class="col-lg-6 col-md-12 d-flex align-items-center">
                              <div class="slit d-flex justify-content-center align-items-center">
                                <img src="../assets/images/slit.png" alt="" class="gap" v-bind:style="{ width: slit+'px' }">
                              </div>
                            </div>
                        </div>
                    </li>
                    <li class="list-group-item d-flex align-items-center">
                        <h5 class="card-title pr-3">Mirror State: </h5>
                        <button
                            class="btn btn-primary"
                            @click="MirrorState()"
                            v-if="Mirror == false"
                        >
                            Place Mirror
                        </button>
                        <button
                            class="btn btn-danger"
                            @click="MirrorState()"
                            v-else
                        >
                            Remove Mirror
                        </button>
                    </li>
                    <li class="list-group-item d-flex align-items-center">
                        <h5 class="card-title pr-3">Grating State: </h5>
                        <button
                            class="btn btn-primary"
                            @click="gratingState()"
                            v-if="Grating == false"
                        >
                            Place Grating
                        </button>
                        <button
                            class="btn btn-danger"
                            @click="gratingState()"
                            v-else
                        >
                            Remove Grating
                        </button>
                    </li>
                    
                </ul>
              
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

