    gratingState() {
      this.Grating = !this.Grating;
      if(this.Mirror == true)
      {
        this.Mirror = !this.Mirror;
      }
      else{
        console.log(" ")
      }
    },

    MirrorState() {
      this.Mirror = !this.Mirror;
      if(this.Grating == true)
      {
        this.Grating = !this.Grating;
      }
      else{
        console.log(" ")
      }
    },

    ////////////////////////////////////////////////////////////////////////////////////Telescope rotation
    telescopeClockwise() {
      if (this.configChavronClockwise.rotation >= 90) {
        this.configChavronClockwise.rotation = 90;
      } else {
        this.configChavronClockwise.rotation = this.configChavronClockwise.rotation + 5;
        this.configTelescope.rotation = this.configTelescope.rotation + 5;
        this.configChavronAntiClockwise.rotation = this.configChavronAntiClockwise.rotation + 5;
        this.configCalibrate.rotation = this.configCalibrate.rotation + 5;
        this.configCalibrateZoom.rotation = this.configCalibrateZoom.rotation + 5;
        this.configSlitLight.x = this.configSlitLight.x + 30;
        this.configSlitLightMirror.x = this.configSlitLightMirror.x + 30;
        

        this.configViolet.x = this.configViolet.x + 10;
        this.configBlue.x = this.configBlue.x + 10;
        this.configGreen.x = this.configGreen.x + 10;
        // this.configYellow.x = this.configYellow.x + 10;
        this.configViolet2.x = this.configViolet2.x + 10;
        this.configBlue2.x = this.configBlue2.x + 10;
        this.configGreen2.x = this.configGreen2.x + 10;
        // this.configYellow2.x = this.configYellow2.x + 10;
        if(this.configTelescope.rotation > 0){
          this.configViolet.opacity = 0;
          this.configBlue.opacity = 0;
          this.configGreen.opacity = 0;
          // this.configYellow.opacity = 0;

          this.configViolet2.opacity = 1;
          this.configBlue2.opacity = 1;
          this.configGreen2.opacity = 1;
          // this.configYellow2.opacity = 1;
        }
        else if (this.configTelescope.rotation == 0) {
          this.configViolet2.opacity = 1;
          this.configBlue2.opacity = 1;
          this.configGreen2.opacity = 1;
          // this.configYellow2.opacity = 1;
          this.configViolet.opacity = 1;
          this.configBlue.opacity = 1;
          this.configGreen.opacity = 1;
          // this.configYellow.opacity = 1;
        }
      }
    },
    telescopeAntiClockwise() {
      if (this.configChavronAntiClockwise.rotation <= -90) {
        this.configChavronAntiClockwise.rotation = -90;
      } else {
        this.configChavronClockwise.rotation = this.configChavronClockwise.rotation - 5;
        this.configTelescope.rotation = this.configTelescope.rotation - 5;
        this.configChavronAntiClockwise.rotation = this.configChavronAntiClockwise.rotation - 5;
        this.configCalibrate.rotation = this.configCalibrate.rotation - 5;
        this.configCalibrateZoom.rotation = this.configCalibrateZoom.rotation - 5;
        
        this.configSlitLight.x = this.configSlitLight.x - 30;
        this.configSlitLightMirror.x = this.configSlitLightMirror.x - 30;


        this.configViolet.x = this.configViolet.x - 10;
        this.configBlue.x = this.configBlue.x - 10;
        this.configGreen.x = this.configGreen.x - 10;
        // this.configYellow.x = this.configYellow.x - 10;
        this.configViolet2.x = this.configViolet2.x - 10;
        this.configBlue2.x = this.configBlue2.x - 10;
        this.configGreen2.x = this.configGreen2.x - 10;
        // this.configYellow2.x = this.configYellow2.x - 10;

        if(this.configTelescope.rotation < 0){
          this.configViolet2.opacity = 0;
          this.configBlue2.opacity = 0;
          this.configGreen2.opacity = 0;
          // this.configYellow2.opacity = 0;
          this.configViolet.opacity = 1;
          this.configBlue.opacity = 1;
          this.configGreen.opacity = 1;
          // this.configYellow.opacity = 1;
        }
        else if (this.configTelescope.rotation == 0) {
          this.configViolet2.opacity = 1;
          this.configBlue2.opacity = 1;
          this.configGreen2.opacity = 1;
          // this.configYellow2.opacity = 1;
          this.configViolet.opacity = 1;
          this.configBlue.opacity = 1;
          this.configGreen.opacity = 1;
          // this.configYellow.opacity = 1;
        }
      }
    },
    ////////////////////////////////////////////////////////////////////////////////////////Table rotation
    tableAntiClockwise() {
      this.configSlitTableLeft.rotation = this.configSlitTableLeft.rotation - 15;
      this.configSlitTableRight.rotation = this.configSlitTableRight.rotation - 15;
      this.configTable.rotation = this.configTable.rotation - 15;
      this.configGratingTable.rotation = this.configGratingTable.rotation - 15;
      // this.configSlitLight.x = this.configSlitLight.x - 30;
      this.configCalibrateZoom.rotation = this.configCalibrateZoom.rotation - 15;
      this.configMirrorTable.rotation = this.configMirrorTable.rotation - 15;
        this.configSlitLightMirror.x = this.configSlitLightMirror.x - 180;


      this.configViolet.x = this.configViolet.x - 30;
        this.configBlue.x = this.configBlue.x - 30;
        this.configGreen.x = this.configGreen.x - 30;
        // this.configYellow.x = this.configYellow.x - 30;
        this.configViolet2.x = this.configViolet2.x - 30;
        this.configBlue2.x = this.configBlue2.x - 30;
        this.configGreen2.x = this.configGreen2.x - 30;
        // this.configYellow2.x = this.configYellow2.x - 30;
    },
    tableClockwise() {
      this.configSlitTableRight.rotation = this.configSlitTableRight.rotation + 15;
      this.configSlitTableLeft.rotation = this.configSlitTableLeft.rotation + 15;
      this.configTable.rotation = this.configTable.rotation + 15;
      this.configGratingTable.rotation = this.configGratingTable.rotation + 15;
      // this.configSlitLight.x = this.configSlitLight.x + 30;
      this.configCalibrateZoom.rotation = this.configCalibrateZoom.rotation + 15;
      this.configMirrorTable.rotation = this.configMirrorTable.rotation + 15;
        this.configSlitLightMirror.x = this.configSlitLightMirror.x + 180;


      this.configViolet.x = this.configViolet.x + 30;
        this.configBlue.x = this.configBlue.x + 30;
        this.configGreen.x = this.configGreen.x + 30;
        // this.configYellow.x = this.configYellow.x + 30;
        this.configViolet2.x = this.configViolet2.x + 30;
        this.configBlue2.x = this.configBlue2.x + 30;
        this.configGreen2.x = this.configGreen2.x + 30;
        // this.configYellow2.x = this.configYellow2.x + 30;
    },
    ////////////////////////////////////////////////////////////////////////////////////////Telescope focus
    focusRight() {
      this.telescopeFocus++;
    },
    focusLeft() {
      this.telescopeFocus--;
    },
    ///////////////////////////////////////////////////////////////////////////////////////Slit size
    slitOpen()
    {
      this.slit++;
    },
    slitClose()
    {
      this.slit--;
    }
  },
  created: function() {
    this.$parent.$on('update', this.setValue)
  },
 
};
</script>

