<template>
  <div class="Footer">
      <div class="container-fluid">
          <div class="row pt-5 pb-4">
              <div class="col-md-4 col-sm-12">
                  <p>© All rights reserved to Ain Shams University 2015-2020</p>
              </div>
              <div class="col-md-4 col-sm-12">
                <div class="form-group d-flex align-items-center">
                    <span v-on:click="prevPage"><i class="fas fa-caret-left fa-2x mr-3"></i></span>
                        <input class="form-control input-sm text-center" id="inputsm" type="text" :value="pageNumber" readonly>
                    <span v-on:click="nextPage"><i class="fas fa-caret-right fa-2x ml-3"></i></span>
                </div>
              </div>
              <div class="col-md-4 col-sm-12">
                  <div class="wrapper float-right">
                    <div class="form-group d-flex align-items-center">
                        <label for="Navigation" class="Navigation">Go to: </label>
                        <select class="form-control" @change="onChange($event)">
                            <option value="1" :selected="pageNumber == 1">Welcome Page</option>
                            <option value="2" :selected="pageNumber == 2">Instruction Page</option>
                            <option value="3" :selected="pageNumber == 3">Experiment Page</option>
                        </select>
                    </div>
                  </div>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'Footer',
  props: {
    pageNumber: Number
  },
  methods: {
      prevPage() {
          this.$emit('prev-page', this.pageNumber-1 );
      },
       nextPage() {
          this.$emit('next-page', this.pageNumber+1 );
      },
      onChange() {
          this.$emit('selected-page', parseInt(event.target.value) );
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
p{
    font-size:1vw; 
}
option{
    font-size:1vw; 
}
.wrapper{
    width: 100%;
}
.Navigation{ 
    width: 60px;
}
span{
    cursor: pointer;
}
.row{
    background: url('../assets/images/footer.jpg') no-repeat;
    background-size: cover;
}
</style>
