<template>
  <div id="app">
    <Header :pageNumber="pageNumber" :style="[pageNumber >= 2 ? {display: 'block'}: {display: 'none'}]"/>
    <WelcomePage :style="[pageNumber == 1 ? {display: 'block'}: {display: 'none'}]"/>
    <InstructionPage :style="[pageNumber == 2 ? {display: 'block'}: {display: 'none'}]"/>
    <Experiment :pageNumber="pageNumber" :style="[pageNumber == 3 ? {display: 'block'}: {display: 'none'}]"/>
    <Answer :style="[pageNumber == 4 ? {display: 'block'}: {display: 'none'}]"/>
    <Result :style="[pageNumber == 5 ? {display: 'block'}: {display: 'none'}]"/>
    <Footer :pageNumber="pageNumber" v-on:prev-page="updatePageNumber($event)" v-on:next-page="updatePageNumber($event)" @selected-page="updatePageNumber($event)"/>
  </div>
</template>

<script>
import Header from './components/Header.vue'
import WelcomePage from './components/WelcomePage.vue'
import InstructionPage from './components/InstructionPage.vue'
import Experiment from './components/Experiment.vue'
import Footer from './components/Footer.vue'
import Answer from './components/Answer.vue'
import Result from './components/Result.vue'


export default {
  name: 'App',
  components: {
    Header,
    WelcomePage,
    InstructionPage,
    Experiment,
    Footer,
    Answer,
    Result
  },
  data(){
    return {
      pageNumber: 1
    }
  },
  methods: {
    updatePageNumber(updatedNumber)
    {
      if (updatedNumber > 0 && updatedNumber < 6) {
        this.pageNumber = updatedNumber;
      }
      if (this.pageNumber == 3){
        this.$emit('update', false); 
      }
    }
  },
  
}
</script>

