<template>
  <div class="Header">
    <div class="container-fluid">
        <div class="row banner py-2">
            <div class="col-3">
                <img src="../assets/images/e-lab-logo.png" alt="" width="37%">
            </div>
            <div class="col-6 d-flex align-items-center justify-content-center">
                <h1>Diffraction Grating Experiment</h1>
            </div>
            <div class="col-3 d-flex align-items-center justify-content-end">
                <div class="icons float-right">
                    <span class="mr-2">
                        <img src="../assets/images/1.png" alt="">
                    </span>
                    <span class="mr-2">
                        <img src="../assets/images/2.png" alt="">
                    </span>
                    <span class="mr-2">
                        <img src="../assets/images/3.png" alt="">
                    </span>
                </div>
            </div>
        </div>
        <div class="row subBar py-2">
            <div class="col-4">
                <h4>Page Tilte</h4>
            </div>
            <div class="col-4"></div>
            <div class="col-4 d-flex justify-content-end">
                <h4 v-if="pageNumber >= 3">Remaining Time ( {{ minutes }} : {{ seconds }} )</h4>
                <h4 v-else>Remaining Time ( 00 : 00 )</h4>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Header',
  props: {
      pageNumber: Number
  },
  data() {
        return {
            seconds: 60,
            minutes: 30,
            check: true
        }
    },

    watch: {

        minutes: {
            handler(value) {

                if (value > 0) {
                    setTimeout(() => {
                        this.minutes--;
                    }, 60000);
                }

            },
            immediate: true // This ensures the watcher is triggered upon creation
        },

        seconds: {
            handler(value) {
                if(this.minutes >= 0 && this.seconds == 0)
                {
                    if(this.check == true){
                        this.seconds = 60
                    }
                }
                if(this.minutes == 0)
                {
                    this.check = false
                }
                if (value > 0) {
                    setTimeout(() => {
                        this.seconds--;
                    }, 1000);
                }

            },
            immediate: true // This ensures the watcher is triggered upon creation
        }

    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  font-size:2.2vw;
}
h4 {
  font-size:1.3vw;
}
.banner{
    background: url('../assets/images/header.jpg') no-repeat;
    background-size: cover;
}
.icons span img{
      width: 1.8vw;
      cursor: pointer;
}
.subBar{
    background-color: #dae7f3;
}
</style>
